<template>
    <div>
        <el-dialog title="计划总结" :visible.sync="value" width="500px" append-to-body :before-close="dialogClose" class="dialog-summary" >
            <el-form :model="summaryForm" :rules="summaryFormRule" ref="summaryForm">
                <div class="box-flex fs12 mb10">
                    <div class="fc6"><span class="fc-red vm mr10">*</span>计划总结：</div>
                    <div class="flex-one tr fc9">总结发布后，不允许修改</div>
                </div>
                <el-form-item prop="plan_summary" class="mb10">
                    <el-input type="textarea" v-model="summaryForm.plan_summary" :rows="5" placeholder="请填写计划相关的工作总结"></el-input>
                </el-form-item>
                <el-form-item label="上传附件：" class="mb0">
                    <el-upload class="upload-demo" action="https://jsonplaceholder.typicode.com/posts/" :file-list="fileList" :limit="10" :on-success="onSuccess" :on-remove="handleRemove" :on-preview="handlePreview" multiple>
                        <el-button size="small" type="text">点击上传</el-button>
                    </el-upload>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer tc">
                <el-button @click="dialogClose">取 消</el-button>
                <el-button type="primary" @click="confirm">确 定</el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
import apis from '@/api'
    export default {
        data() {
            return {
                summaryForm: {
                    plan_summary: '',
                    attaches: [],
                },
                summaryFormRule:{
                    plan_summary:[
                        {required:true,message:'请填写计划相关的工作总结'},
                        {min:10,message:'最少10个字'}
                    ]
                },
                fileList:[]
            }
        },
        props: {
            value: {
                type: Boolean,
                default: false
            },
            planId: {
                type: String,
                default: '',
            }
        },
        watch: {
            fileList: function(nv, ov) {
                this.summaryForm.attaches = []
                nv.forEach((item, index) => {
                    this.summaryForm.attaches.push({
                        attach_name: item.name,
                        attach_path: item.url,
                        attach_size: item.size
                    })
                });
            },
        },
        methods: {
            dialogClose() {
                this.dialogVisible = false;
                this.$emit("input", false);
            },
            async confirm() {
                let vm=this
                this.$refs.summaryForm.validate((valid) => {
                    if (valid) {
                        apis.plan.putPlanSummary(vm.planId,vm.summaryForm).then(() => {
                            this.dialogClose();
                            this.$emit("reload-list");
                        })
                    } else {
                        return false
                    }
                })
    
            },
            onSuccess(rs, file, fileList) {
                let vm = this
                vm.fileList = fileList
            },
            handleRemove(file, fileList) {
                this.fileList = fileList
            },
            handlePreview(file) {
                console.log(file);
            },
            handleExceed(files, fileList) {
                this.$message.warning(
                    `当前限制选择 10 个文件，本次选择了 ${
                files.length
              } 个文件，共选择了 ${files.length + fileList.length} 个文件`
                );
            },
            beforeRemove(file, fileList) {
                return this.$confirm(`确定移除 ${file.name}？`);
            }
        }
    }
</script>
<style lang="scss">
.dialog-summary{
    .el-dialog__header{
        border-bottom:1px solid #dedede;
    }
    .el-dialog__body{
        padding:20px 20px 10px 20px
    }
}
</style>


